* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.spinner {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: rgb(105, 34, 34);
}

.form-d {
  /* color: white;
     */
  margin: 12px;
}
table {
  /* border: 4px solid rgb(7, 2, 2); */
  width: 80%;
height: 34vh;
  margin: 35vh auto;
  text-align: center;
}

table thead td{
    border: 2px solid rgb(56, 61, 57);
    background-color: antiquewhite;
    color: black;
}table tbody td{
    border: 2px solid black;
}

.p-form{
  padding: 12px;
  border: 2px solid black;
  width: 50%;
  box-shadow: 2px 2px black;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
  margin: 12vh auto;
}