.c-layout {
  height: 100vh;
  background-color: rgb(75, 50, 50);
  color: white;
  width: 100%;
}
.sideBar {
    width: 20%;
  /* margin: auto; */
  margin: 2vh 2vh;
  justify-content: center;
  border: 2px solid rgb(228, 218, 218);
}
.logo {
  justify-content: center;
  border: 2px solid red;
  font-size: 5vh;
  font-style: italic;
  text-align: center;
}
.menu {
  /* background-color: cadetblue; */
  margin: 9px;
  align-items: center;
  justify-content: center;
  height: 85vh;
}
.body {
    width: 80%;
  margin: 2vh 2vh; /* width: 100%; */
  /* height: 100%; */
  /* justify-content: center; */
}
.header {
  height: 12vh;
  border: 2px solid white;
  box-shadow: 2px 2px white;

}
.i-body {
  margin: 12px auto;
  border: 2px solid white;
  /* width: 100%; */
  height: 86%;
  box-shadow: 2px 2px white;
}
.menu-items{
  /* text-align: center; */
  margin-top: 30px;
  list-style: none;
  justify-content: center;
  /* text-decoration: none; */
  color: white;
  /* border: 3px solid red; */
}

.menu-items a{
  text-decoration: none;
  /* padding: 3px; */
  font-size: 1rem;
  margin: auto 8px;
  /* border: 1px solid white; */
  color: white;
}
.menu-items i{
  font-size: 1rem;
}
.active{
  background-color: rgb(27, 31, 31);
}
.header-content{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 0 12px;
}
.profile-icon{
  color: white;
  font-size: 1.5rem;
  font-style: italic;
  text-decoration: none;
  margin: 12px;
}
